<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <v-app-bar-nav-icon></v-app-bar-nav-icon>
      <v-toolbar-title>
        <v-btn text link :ripple="false" to="/home">
          Car Keeper
        </v-btn>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn v-if="token.length < 1" text to="/login">
        Login
      </v-btn>

      <v-btn v-if="token.length < 1" text to="/signup">
        Sign Up
      </v-btn>

      <v-btn v-if="token.length > 0" text to="/dashboard">
        Dashboard
      </v-btn>

      <v-btn v-if="token.length > 0" text @click="logOut">
        Logout
      </v-btn>
    </v-app-bar>

    <v-content>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>

export default {
  name: 'App',

  computed: {
    token() {
      return this.$store.state.token;
    }
  },

  methods: {
    logOut() {
      this.$store.commit("clearToken");
      this.$router.push("/home");
    }
  }
};
</script>
