<template>
    <v-container>
        <v-row>
            <v-col class="title text-center">Welcome {{ name }}!</v-col>
        </v-row>
        <v-row justify="space-around">
            <v-col class="display-2 text-center" cols=12>
                Cars
            </v-col>
            <v-col md=3 cols=12 v-for="car in cars" :key="car._id">
                <v-card shaped elevation=5 @click="setCurrentCar(car)" link>
                    <v-container>
                        <v-row>
                            <v-col class="text-center display-1">
                                <v-icon>fas fa-car</v-icon> {{ car.name }} <v-icon>fas fa-car</v-icon>
                            </v-col>
                        </v-row>
                        <v-row class="text-center">
                            <v-col class="title">
                                <v-list disabled>
                                    <v-list-item-group>
                                        <v-list-item inactive>
                                            <v-list-item-content>
                                                <v-list-item-title>{{ car.year }}</v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item inactive>
                                            <v-list-item-content>
                                                <v-list-item-title>{{ car.make }}</v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item inactive>
                                            <v-list-item-content>
                                                <v-list-item-title>{{ car.model }}</v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                        <v-list-item inactive>
                                            <v-list-item-content>
                                                <v-list-item-title>{{ car.mileage }}</v-list-item-title>
                                            </v-list-item-content>
                                        </v-list-item>
                                    </v-list-item-group>
                                </v-list>
                            </v-col>
                            <!-- <v-col xl=3 cols=12 class="title">
                                {{ car.year }}
                            </v-col>
                            <v-col xl=3 cols=12 class="title">
                                {{ car.make }}
                            </v-col>
                            <v-col xl=3 cols=12 class="title">
                                {{ car.model }}
                            </v-col>
                            <v-col xl=3 cols=12 class="title">
                                {{ car.mileage }}
                            </v-col> -->
                        </v-row>
                        <v-row justify="center">
                            <v-col cols=6 class="text-center">
                                <v-btn color="primary" block @click.stop="">
                                    Edit
                                </v-btn>
                            </v-col>
                            <v-col cols=6 class="text-center">
                                <v-btn color="error" block @click.stop="">
                                    Delete
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
        <v-row justify="space-around">
            <v-col cols=12 md=3>
                <v-card elevation=3>
                    <v-container fluid>
                        <v-row>
                            <v-col>
                                <v-card-title>
                                    <v-spacer></v-spacer>
                                    Add MPG Entry
                                    <v-spacer></v-spacer>
                                </v-card-title>
                            </v-col>
                        </v-row>
                        <v-row justify="center">
                            <v-col cols=11>
                                <v-text-field v-model="miles" class="mx-auto" label="Miles"></v-text-field>
                            </v-col>
                            <v-col cols=11>
                                <v-select :items="typesOfGas" v-model="gasType" label="Gas Type"></v-select>
                            </v-col>
                            <v-col cols=11>
                                <v-text-field v-model="gallons" class="mx-auto" label="Gallons"></v-text-field>
                            </v-col>
                            <v-col cols=11>
                                <v-text-field v-model="price" class="mx-auto" label="Price"></v-text-field>
                            </v-col>
                            <v-col cols=11>
                                <v-select v-model="selectedCar" :items="carNames" item-text="name" item-value="id" label="Select Car"></v-select>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-card-actions>
                        <v-btn block dark color="blue" @click="addMpgEntry" :loading="loading">
                            Submit
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
        <v-progress-circular indeterminte v-if="loadingDialog"></v-progress-circular>
    </v-container>
</template>

<script>
import rp from "request-promise-native";

// const EDWARDS = {
//     lat: 34.920190, 
//     long: -117.927867
// };

export default {
    // Prevent the user from accessing the dashboard if they are not logged in.
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if (vm.$store.state.token.length < 1) {            
                next("/login");
            } else {
                next();
            }
        })
    },
    data() {
        return {
            typesOfGas: ["Regular", "Extra", "Super/Premium"],
            loading: false,
        }
    },
    computed: {
        name() {
            return this.$store.state.name;
        },
        cars: {
            get() {
                return this.$store.state.cars;
            }
        },
        selectedCar: {
            get() {
                return this.$store.state.selectedCar;
            },
            set(val) {
                this.$store.commit("setSelectedCar", val);
            }
        },
        miles: {
            get() {
                if (this.$store.state.mpgEntry.miles === 0) {
                    return "";
                } else {
                    return this.$store.state.mpgEntry.miles;
                }
            },
            set(val) {
                this.$store.commit("setMiles", val);
            }
        },
        gallons: {
            get() {
                if (this.$store.state.mpgEntry.gallons === 0) {
                    return "";
                } else {
                    return this.$store.state.mpgEntry.gallons;
                }
            },
            set(val) {
                this.$store.commit("setGallons", val);
            }
        },
        price: {
            get() {
                if (this.$store.state.mpgEntry.price === 0) {
                    return "";
                } else {
                    return this.$store.state.mpgEntry.price;
                }
            },
            set(val) {
                this.$store.commit("setPrice", val);
            }
        },
        gasType: {
            get() {
                return this.$store.state.mpgEntry.type;
            },
            set(val) {
                this.$store.commit("setType", val);
            }
        },
        location() {
            return this.$store.state.mpgEntry.location;
        },
        carNames() {
            return this.$store.getters.getCarNames;
        },
        loadingDialog: {
            get() {
                return this.$store.state.loadingDialog;
            },
            set() {
                this.$store.commit("showLoadingDialog");
            }
        }
    },
    methods: {
        async addMpgEntry() {
            // Start the loading symbol on the button
            this.loading = true;
            console.log("In the function...");
            
            // Get the Vue instance
            let self = this;
            // Get the location and wait for that
            await self.$store.dispatch("getLocation");

            console.log('After the await');
            
            
            // Options for request-promise-native
            let options = {
                method: "POST",
                uri: `${process.env.VUE_APP_HOST}/cars/${self.$store.state.selectedCar}/mpg`,
                auth: {
                    bearer: self.$store.state.token
                },
                body: {
                    miles: self.$store.state.mpgEntry.miles,
                    gallons: self.$store.state.mpgEntry.gallons,
                    price: self.$store.state.mpgEntry.price,
                    gasType: self.$store.state.mpgEntry.type,
                    location: {
                        latitude: self.$store.state.mpgEntry.location.latitude,
                        longitude: self.$store.state.mpgEntry.location.longitude
                        // latitude: EDWARDS.lat,
                        // longitude: EDWARDS.long
                    }
                },
                json: true
            };

            console.log(options);
            
            
            rp(options)
            .then(parsedBody => {
                console.log(parsedBody);
                
            })
            .catch(err => {
                console.log(err);
                
            })
        },
        setCurrentCar(car) {
            // Set the selected car
            this.$store.commit("setSelectedCar", car);

            // Push to the correct location.
            this.$router.push({ name: 'specificCar', params: {carId: car._id}});
        }
    },
    mounted: async function() {
        // Check to see if the cars have been loaded.
        if (this.$store.state.cars.length < 1) {
            // Get the users location
            await this.$store.dispatch("loadCars", this.$store.state.token);
        }

        // Proceed with everything else
    }
}
</script>