<template>
    <v-container fluid>
        <v-row>
            <v-col cols=12 class="display-3 font-weight-medium">
                <v-toolbar color="orange darken-2" dark>
                    <v-toolbar-title>{{ selectedCar.name }}</v-toolbar-title>
                </v-toolbar>
            </v-col>
        </v-row>
        <!-- Average MPG -->
        <v-row justify="center">
            <v-col cols=12 md=5 class="text-center">
                <v-progress-circular indeterminate size="64" width="10" v-if="loading"></v-progress-circular>
                <v-card v-if="!loading">
                    <v-container fluid>
                        <v-row justify="center">
                            <v-col class="headline font-weight-medium text-center" cols=12>
                                Average MPG
                            </v-col>
                            <v-col class="text-center" cols=12 md=1>
                                <v-icon x-large>fas fa-tachometer-alt fa-3x</v-icon>
                            </v-col>
                            <v-col class="display-1" cols=12 md=3>{{ avg }}</v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
        <!-- MPG Entries -->
        <v-row justify="space-around">
            <v-col class="text-center" v-for="entry in selectedCar.mpg" :key="entry._id" md=3>
                <v-card>
                    <v-card-title>
                        <v-icon>fas fa-gas-pump</v-icon>
                        <v-spacer></v-spacer>
                        Details
                        <v-spacer></v-spacer>
                        <v-icon>fas fa-gas-pump</v-icon>
                    </v-card-title>
                    <v-container>
                        <v-row>
                            <v-col md>{{ formattedDate(entry.date) }}</v-col>
                            <v-col md>${{ entry.price }}</v-col>
                        </v-row>
                        <v-row>
                            <v-col cols=12 sm=6>Miles: {{ entry.miles }}</v-col>
                            <v-col cols=12 sm=6>Gals: {{ entry.gallons }}</v-col>
                            <v-col cols=12 sm=6>Gas Type: {{ entry.gasType }}</v-col>
                            <v-col cols=12 sm=6>MPG: {{ entry.mpg }}</v-col>
                        </v-row>
                        <v-row>
                            <v-col cols=12><v-divider></v-divider></v-col>
                        </v-row>
                        <v-row>
                            <v-col cols=12>
                                Gas Station
                            </v-col>
                            <v-col cols=12 sm=6>{{ entry.gasStation.name }}</v-col>
                            <v-col cols=12 sm=6>Address: {{ entry.gasStation.vicinity }}</v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import rp from "request-promise-native"

export default {
    data() {
        return {
            loading: true,
            avg: 0
        }
    },
    computed: {
        selectedCar() {
            return this.$store.state.selectedCar;
        }
    },
    methods: {
        formattedDate(d) {
            let newD = new Date(d);
            return newD.toLocaleDateString();
        }
    },
    mounted: async function () {
        let self = this;

        // Options for request-promise-native
        let options = {
            method: "GET",
            uri: `${process.env.VUE_APP_HOST}/cars/${this.$store.state.selectedCar._id}/avgMpg`,
            auth: {
                bearer: self.$store.state.token
            },
            json: true
        };
        
        let res = await rp(options); 
        self.avg = res.avg;
        self.loading = false;
    }
    
}
</script>